export default {
	data() {
		return {
			user: undefined
		}
	},
	onShow() {

		if (uni.getStorageSync('token')) this.login();




	},
	methods: {

		auth() {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					uni.getUserInfo({
						provider: 'weixin',
						success: (info_res) => {
							this.$api.auth(res.code, info_res.encryptedData, info_res.iv).then(auth_res => {
								if (auth_res.data.code === 2000) {
									this.user = {
										'avatar': auth_res.data.data.avatar,
										'name': auth_res.data.data.name
									}
									uni.setStorageSync('token', auth_res.data.data.token);
								} else {
									uni.showToast({
										title: '登陆失败，系统维护中',
										icon: 'none'
									})
								}
							})
						}
					})
				}
			})
		},


		login() {
			this.$api.getInfo().then(res => {
				if (res.data.code === 2000) {
					this.user = {
						'avatar': res.data.data.avatar,
						'name': res.data.data.name
					}
				} else {
					this.user = undefined;
					uni.showToast({
						title: '登陆过期，请重新登录',
						icon: 'none'
					})
				}
			})
		}

	}
}
